import { _decorator, Component, Node } from 'cc';
import { TelegramWebApp } from './telegram-web';
import { UserMainData } from './UserInterfaces';
import { UserDataManager } from './UserDataManager';
import { SlidePopup } from './SlidePopup';
import { TypewriterEffect } from './TypewriterEffect';
const { ccclass, property } = _decorator;

@ccclass('OnboardingController')
export class OnboardingController extends Component {

    @property(SlidePopup)
    afterChooseSideSlidePopup: SlidePopup = null;

    @property(TypewriterEffect)
    afterChooseSideTypewriter: TypewriterEffect = null;

    @property(SlidePopup)
    beforeChooseSideSlidePopup: SlidePopup = null;

    @property(TypewriterEffect)
    withNameTypewriter: TypewriterEffect = null;

    @property
    prefixWithNameTypewriter: string = "Теперь ты готов начать своё приключение. Удачи, ";

    @property
    postfixWithNameTypewriter: string = "!";

    isZombie: boolean = false;

    public clearUserData(){
        UserDataManager.Instance.removeUserData((error, success) => {
            if (error) {
                console.error("Error when deleting user data:", error);
                TelegramWebApp.Instance.alert("Error when deleting user data");
            } else {
                console.log("The user's data has been successfully deleted:", success);
                TelegramWebApp.Instance.alert("The user's data has been successfully deleted");
            }
        });
    }

    public showZobmiesConfirm() {
        UserDataManager.Instance.getUserData((error, data) => {
            if (error) {
                console.error("Error when receiving user data:", error);
                TelegramWebApp.Instance.alert("Error when receiving user data");
            } else if (data) {
                console.log("Received user data:", data);
                const isZombieDataValue = data.isZombie;
                TelegramWebApp.Instance.alert("You are zombie - " + isZombieDataValue);
            } else {
                this.isZombie = true;
                const message = "Ты выбрал присоединиться к Зомби. Изменить выбор после подтверждения будет невозможно.";
                TelegramWebApp.Instance.confirm(message, this.onConfirmAnswered.bind(this));
            }
        });
    }

    public showHumansConfirm() {
        UserDataManager.Instance.getUserData((error, data) => {
            if (error) {
                console.error("Error when receiving user data:", error);
                TelegramWebApp.Instance.alert("Error when receiving user data");
            } else if (data) {
                console.log("Received user data:", data);
                const isZombieDataValue = data.isZombie;
                TelegramWebApp.Instance.alert("You are zombie - " + isZombieDataValue);
            } else {
                this.isZombie = false;
                const message = "Ты выбрал присоединиться к Людям. Изменить выбор после подтверждения будет невозможно.";
                TelegramWebApp.Instance.confirm(message, this.onConfirmAnswered.bind(this));
            }
        });
    }

    onConfirmAnswered(confirm: boolean) {
        if (confirm) {
            this.onConfirmed();
        }
        else {
            this.onChaneled();
        }
    }

    onConfirmed() {
        let myName = TelegramWebApp.Instance.getTelegramUser().first_name;

        if (!myName) {
            myName = "Anonymous"
        }

        const userData: UserMainData = {
            name: myName,
            balance: 0,
            level: 1,
            isZombie: this.isZombie
        };

        UserDataManager.Instance.saveUserData(userData, (error, success) => {
            if (error) {
                console.error("Error saving user data:", error);
            } else {
                console.log("The user's data has been successfully saved:", success);

                this.afterChooseSideTypewriter.ShowText();

                this.afterChooseSideSlidePopup.show();

                this.beforeChooseSideSlidePopup.hide();

                this.withNameTypewriter.fullText = this.prefixWithNameTypewriter + myName + this.postfixWithNameTypewriter;
            }
        });
    }

    onChaneled() {

    }
}